.t-label {
  cursor: pointer;
  font-weight: 700;
}

.t-control {
  display: block;
  width: 100%;
}

.t-field {
  margin-bottom: 1em;
}

@media (min-width: 782px) {
  .t-field--inline {
    display: flex;
  }
  .t-field--inline .t-label {
    padding-top: 0.4em;
    flex-basis: 150px;
    flex-grow: 0;
    flex-shrink: 0;
  }
  .t-field--inline .t-control input[type="checkbox"],
  .t-field--inline .t-control input[type="radio"] {
    margin-top: 0.4em;
  }
}

.t-control-group--inline {
  display: flex;
  align-items: center;
}
